import {
  Routes,
  Route,
  useNavigationType,
  useLocation,
} from "react-router-dom";
import HOMEPAGE from "./pages/HOMEPAGE";
import CoachPage from "./pages/CoachPage";
import SCHOLARSHIPSDESCRIPTION1 from "./pages/SCHOLARSHIPSDESCRIPTION1";
import Dashboard from "./pages/Dashboard";
import Communities from "./pages/Communities";
import BasedOnProfile from "./pages/BasedOnProfile";
import BasedOnMajor from "./pages/BasedOnMajor";
import Popular from "./pages/Popular";
import Services from "./pages/Services";
import SCHOLARSHIPSCATEGORYPAGE from "./pages/SCHOLARSHIPSCATEGORYPAGE";
import EDUCATIONPROFILE from "./pages/EDUCATIONPROFILE";
import STUDENTACTIVITIESPROFILE from "./pages/STUDENTACTIVITIESPROFILE";
import PERSONALPROFILE from "./pages/PERSONALPROFILE";
import Login from "./pages/Login";
import SIGNUP from "./pages/SIGNUP";
import Welcome from "./pages/Welcome";
import { useEffect } from "react";

function App() {
  const action = useNavigationType();
  const location = useLocation();
  const pathname = location.pathname;

  useEffect(() => {
    if (action !== "POP") {
      window.scrollTo(0, 0);
    }
  }, [action, pathname]);

  useEffect(() => {
    let title = "";
    let metaDescription = "";

    switch (pathname) {
      case "/":
        title = "";
        metaDescription = "";
        break;
      case "/coach-page":
        title = "";
        metaDescription = "";
        break;
      case "/scholarships-description1":
        title = "";
        metaDescription = "";
        break;
      case "/dashboard":
        title = "";
        metaDescription = "";
        break;
      case "/communities":
        title = "";
        metaDescription = "";
        break;
      case "/based-on-profile":
        title = "";
        metaDescription = "";
        break;
      case "/based-on-major":
        title = "";
        metaDescription = "";
        break;
      case "/popular":
        title = "";
        metaDescription = "";
        break;
      case "/services":
        title = "";
        metaDescription = "";
        break;
      case "/scholarships-category-page":
        title = "";
        metaDescription = "";
        break;
      case "/education-profile":
        title = "";
        metaDescription = "";
        break;
      case "/student-activities-profile":
        title = "";
        metaDescription = "";
        break;
      case "/profile":
        title = "";
        metaDescription = "";
        break;
      case "/login":
        title = "";
        metaDescription = "";
        break;
      case "/signup":
        title = "";
        metaDescription = "";
        break;
      case "/onboarding-4":
        title = "";
        metaDescription = "";
        break;
    }

    if (title) {
      document.title = title;
    }

    if (metaDescription) {
      const metaDescriptionTag = document.querySelector(
        'head > meta[name="description"]'
      );
      if (metaDescriptionTag) {
        metaDescriptionTag.content = metaDescription;
      }
    }
  }, [pathname]);

  return (
    <Routes>
      <Route path="/" element={<HOMEPAGE />} />
      <Route path="/coach-page" element={<CoachPage />} />
      <Route
        path="/scholarships-description1"
        element={<SCHOLARSHIPSDESCRIPTION1 />}
      />
      <Route path="/dashboard" element={<Dashboard />} />
      <Route path="/communities" element={<Communities />} />
      <Route path="/based-on-profile" element={<BasedOnProfile />} />
      <Route path="/based-on-major" element={<BasedOnMajor />} />
      <Route path="/popular" element={<Popular />} />
      <Route path="/services" element={<Services />} />
      <Route
        path="/scholarships-category-page"
        element={<SCHOLARSHIPSCATEGORYPAGE />}
      />
      <Route path="/education-profile" element={<EDUCATIONPROFILE />} />
      <Route
        path="/student-activities-profile"
        element={<STUDENTACTIVITIESPROFILE />}
      />
      <Route path="/profile" element={<PERSONALPROFILE />} />
      <Route path="/login" element={<Login />} />
      <Route path="/signup" element={<SIGNUP />} />
      <Route path="/onboarding-4" element={<Welcome />} />
    </Routes>
  );
}
export default App;
